import { Item } from "@/types/Item";

interface Props {
  item: Item;
  onSelect?: (item: Item) => void;
}

const API_BASE_URL =
  process.env.NEXT_PUBLIC_API_URL?.replace(/\/$/, "") || "http://localhost:3001";

const resolveImageUrl = (imagePath?: string | null) => {
  if (!imagePath) return null;
  if (/^https?:\/\//i.test(imagePath)) return imagePath;
  return `${API_BASE_URL}${imagePath.startsWith("/") ? imagePath : `/${imagePath}`}`;
};

export default function ItemCard({ item, onSelect }: Props) {
  const imageSrc = resolveImageUrl(item.image_url);
  const contactName = item.username || item.owner_username;
  const contactEmail = item.email || item.owner_email;
  const contactPhone = item.phone || item.owner_phone;
  const contactAvailable = contactName || contactEmail || contactPhone;
  const isInteractive = Boolean(onSelect);

  const handleKeyDown = (event: React.KeyboardEvent<HTMLDivElement>) => {
    if (!isInteractive) return;
    if (event.key === "Enter" || event.key === " ") {
      event.preventDefault();
      onSelect?.(item);
    }
  };

  const stopPropagation = (event: React.MouseEvent) => {
    if (isInteractive) {
      event.stopPropagation();
    }
  };

  return (
    <div
      className={`bg-white rounded-xl shadow-lg overflow-hidden transition duration-300 ${
        isInteractive ? "cursor-pointer hover:-translate-y-1 hover:shadow-2xl focus-within:ring-2 focus-within:ring-red-200" : "hover:shadow-2xl"
      }`}
      onClick={() => onSelect?.(item)}
      onKeyDown={handleKeyDown}
      role={isInteractive ? "button" : undefined}
      tabIndex={isInteractive ? 0 : undefined}
      aria-label={isInteractive ? `Preview ${item.name}` : undefined}
    >
      {imageSrc && (
        <img src={imageSrc} alt={item.name} className="w-full h-48 object-cover" />
      )}
      <div className="p-4">
        <h2 className="text-xl font-semibold mb-2">{item.name}</h2>
        <p className="text-gray-600 mb-2">{item.description}</p>
        <p className="text-gray-400 text-sm">üìç {item.location}</p>
        <p className="text-gray-400 text-sm">üìÖ {item.date_found}</p>
        <span
          className={`inline-block mt-3 px-3 py-1 rounded-full text-xs font-semibold ${
            item.type === "lost"
              ? "bg-red-200 text-red-800"
              : "bg-green-200 text-green-800"
          }`}
        >
          {item.type.toUpperCase()}
        </span>

        {contactAvailable && (
          <div className="mt-4 rounded-2xl border border-gray-100 bg-gray-50/80 p-4 text-sm">
            <p className="text-xs uppercase tracking-widest text-gray-500 mb-2">
              Contact owner
            </p>
            {contactName && (
              <p className="text-gray-800 font-semibold">{contactName}</p>
            )}
            {contactEmail && (
              <a
                href={`mailto:${contactEmail}`}
                className="mt-1 block text-red-600 hover:underline"
                onClick={stopPropagation}
              >
                {contactEmail}
              </a>
            )}
            {contactPhone && (
              <a
                href={`tel:${contactPhone}`}
                className="mt-1 block text-gray-700 hover:text-gray-900"
                onClick={stopPropagation}
              >
                {contactPhone}
              </a>
            )}
            {!contactEmail && !contactPhone && (
              <p className="text-gray-500 text-xs">
                Owner has not shared direct contact info yet.
              </p>
            )}
          </div>
        )}
      </div>
    </div>
  );
}
