// components/Header.tsx
"use client";

import Link from "next/link";
import { useRouter } from "next/navigation";
import { useState } from "react";

const navLinks = [
  { href: "/", label: "Home" },
  { href: "/lost", label: "Lost Items" },
  { href: "/report?type=lost", label: "Report Lost" },
  { href: "/found", label: "Found Items" },
  { href: "/report?type=found", label: "Report Found" },
  { href: "/profile", label: "Profile" },
];

export default function Header() {
  const router = useRouter();
  const [isMenuOpen, setIsMenuOpen] = useState(false);

  const handleSignOut = () => {
    setIsMenuOpen(false);
    localStorage.removeItem("token");
    router.push("/login");
  };

  return (
    <header className="sticky top-0 z-20 border-b border-gray-100 bg-white shadow-sm">
      <div className="mx-auto flex max-w-6xl items-center justify-between px-4 py-3">
        <Link href="/" className="text-2xl font-extrabold tracking-tight text-gray-900">
          LOFO
        </Link>

        <button
          type="button"
          className="rounded-lg border border-gray-200 p-2 text-gray-600 transition hover:text-gray-900 focus:outline-none focus:ring-2 focus:ring-gray-200 md:hidden"
          aria-label="Toggle navigation menu"
          onClick={() => setIsMenuOpen((prev) => !prev)}
        >
          <svg
            xmlns="http://www.w3.org/2000/svg"
            className={`h-6 w-6 transition ${isMenuOpen ? "rotate-90" : ""}`}
            fill="none"
            viewBox="0 0 24 24"
            stroke="currentColor"
            strokeWidth={2}
          >
            <path
              strokeLinecap="round"
              strokeLinejoin="round"
              d={
                isMenuOpen
                  ? "M6 18L18 6M6 6l12 12"
                  : "M4 6h16M4 12h16M4 18h16"
              }
            />
          </svg>
        </button>

        <nav className="hidden items-center space-x-6 text-sm font-medium md:flex">
          {navLinks.map((link) => (
            <Link key={link.href} href={link.href} className="text-gray-600 transition hover:text-gray-900">
              {link.label}
            </Link>
          ))}
          <button
            onClick={handleSignOut}
            className="rounded-md border border-gray-400 px-4 py-1.5 text-gray-700 transition hover:bg-gray-100"
          >
            Sign Out
          </button>
        </nav>
      </div>

      {isMenuOpen && (
        <div className="border-t border-gray-100 bg-white px-4 py-4 md:hidden">
          <nav className="flex flex-col space-y-3 text-sm font-medium">
            {navLinks.map((link) => (
              <Link
                key={link.href}
                href={link.href}
                onClick={() => setIsMenuOpen(false)}
                className="rounded-lg px-2 py-2 text-gray-700 transition hover:bg-gray-50"
              >
                {link.label}
              </Link>
            ))}
            <button
              onClick={handleSignOut}
              className="rounded-lg border border-gray-300 px-4 py-2 text-left text-gray-700 transition hover:bg-gray-50"
            >
              Sign Out
            </button>
          </nav>
        </div>
      )}
    </header>
  );
}